import React, { useEffect, useState } from 'react';
import HTMLReactParser from 'html-react-parser';
import { useParams } from 'react-router-dom';
import millify from 'millify';
import { Col, Row, Typography, Select } from 'antd';
import { MoneyCollectOutlined, DollarCircleOutlined, FundOutlined, ExclamationCircleOutlined, StopOutlined, TrophyOutlined, CheckOutlined, NumberOutlined, ThunderboltOutlined } from '@ant-design/icons';

import LineChart from './LineChart';

const { Title, Text } = Typography;
const { Option } = Select;

const CryptoDetails = () => {
  const { coinId } = useParams();
  const [timeperiod, setTimeperiod] = useState('7d');

  const [cryptoDetails,ccryptoDetails]=useState(null);
const [coinHistory,ccoinHistory]=useState(null);
  const options = {
    method: 'GET',
    headers: {
      'X-RapidAPI-Key': '69bb7f54damsh01d5d840abba1e9p15f017jsneb351df1d229',
      'X-RapidAPI-Host': 'coinranking1.p.rapidapi.com'
    }
  };
  
  useEffect(()=>{
    fetch(`https://coinranking1.p.rapidapi.com/coin/${coinId}?referenceCurrencyUuid=yhjMzLPhuIDl&timePeriod=24h`, options)
	.then(response => response.json())
	.then(response => {
    
    ccryptoDetails(response);})
	.catch(err => console.error(err));
  },[])



  // const { data: coinHistory } = useGetCryptoHistoryQuery({ coinId, timeperiod });
 useEffect(()=>{
  fetch(`https://coinranking1.p.rapidapi.com/coin/${coinId}/history?referenceCurrencyUuid=yhjMzLPhuIDl&timePeriod=${timeperiod}`, options)
	.then(response => response.json())
	.then(response => ccoinHistory(response))
	.catch(err => console.error(err));
 },[coinId,timeperiod])

  // if (isFetching) return <Loader />;

  if(!cryptoDetails || !cryptoDetails.data || !coinHistory){
    return <div className="lds-ripple"><div></div><div></div></div>
  }

  

  const time = ["3h", "24h", "7d", "30d", "1y", "3m", "3y", "5y"];

  const stats = [
    { title: 'Price to USD', value: `$ ${cryptoDetails.data.coin.price && millify(Number(cryptoDetails.data.coin.price))}`, icon: <DollarCircleOutlined /> },
    { title: 'Rank', value: cryptoDetails.data.coin.rank, icon: <NumberOutlined /> },
    { title: '24h Volume', value: `$ ${millify(Number(cryptoDetails.data.coin['24hVolume']))}`, icon: <ThunderboltOutlined /> },
    { title: 'Market Cap', value: `$ ${millify(Number(cryptoDetails.data.coin.marketCap))}`, icon: <DollarCircleOutlined /> },
    { title: 'All-time-high(daily avg.)', value: `$ ${millify(Number(cryptoDetails.data.coin.allTimeHigh.price))}`, icon: <TrophyOutlined /> },
  ];

  const genericStats = [
    { title: 'Number Of Markets', value: cryptoDetails.data.coin.numberOfMarkets, icon: <FundOutlined /> },
    { title: 'Number Of Exchanges', value: cryptoDetails.data.coin.numberOfExchanges, icon: <MoneyCollectOutlined /> },
    { title: 'Aprroved Supply', value: cryptoDetails.data.coin.approvedSupply ? <CheckOutlined /> : <StopOutlined />, icon: <ExclamationCircleOutlined /> },
    { title: 'Total Supply', value: `$ ${millify(Number(cryptoDetails.data.coin.supply.total))}`, icon: <ExclamationCircleOutlined /> },
    { title: 'Circulating Supply', value: `$ ${millify(Number(cryptoDetails.data.coin.supply.circulating))}`, icon: <ExclamationCircleOutlined /> },
  ];

  return (
    <Col className="coin-detail-container">
      <Col className="coin-heading-container">
        <Title level={2} className="coin-name">
          {cryptoDetails.data.coin.name} ({cryptoDetails.data.coin.slug}) Price
        </Title>
        <p>{cryptoDetails.data.coin.name} live price in US Dollar (USD). View value statistics, market cap and supply.</p>
      </Col>
      <Select defaultValue="7d" className="select-timeperiod" placeholder="Select Timeperiod" onChange={(value) => {setTimeperiod(value)}}>
        {time.map((date,i) => {return <Option key={i} value={date}>{date}</Option>})}
      </Select>
      <LineChart coinHistory={coinHistory} currentPrice={millify(Number(cryptoDetails.data.coin.price))} coinName={cryptoDetails.data.coin.name} />
      <Col className="stats-container">
        <Col className="coin-value-statistics">
          <Col className="coin-value-statistics-heading">
            <Title level={3} className="coin-details-heading">{cryptoDetails.data.coin.name} Value Statistics</Title>
            <p>An overview showing the statistics of {cryptoDetails.data.coin.name}, such as the base and quote currency, the rank, and trading volume.</p>
          </Col>
          {stats.map(({ icon, title, value },i) => (
            <Col className="coin-stats" key={i}>
              <Col className="coin-stats-name">
                <Text>{icon}</Text>
                <Text>{title}</Text>
              </Col>
              <Text className="stats">{value}</Text>
            </Col>
          ))}
        </Col>
        <Col className="other-stats-info">
          <Col className="coin-value-statistics-heading">
            <Title level={3} className="coin-details-heading">Other Stats Info</Title>
            <p>An overview showing the statistics of {cryptoDetails.data.coin.name}, such as the base and quote currency, the rank, and trading volume.</p>
          </Col>
          {genericStats.map(({ icon, title, value },i) => (
            <Col className="coin-stats" key={i}>
              <Col className="coin-stats-name">
                <Text>{icon}</Text>
                <Text>{title}</Text>
              </Col>
              <Text className="stats">{value}</Text>
            </Col>
          ))}
        </Col>
      </Col>
      <Col className="coin-desc-link">
        <Row className="coin-desc">
          <Title level={3} className="coin-details-heading">What is {cryptoDetails.data.coin.name}?</Title>
          {HTMLReactParser(cryptoDetails.data.coin.description)}
        </Row>
        <Col className="coin-links">
          <Title level={3} className="coin-details-heading">{cryptoDetails.data.coin.name} Links</Title>
          {cryptoDetails.data.coin.links?.map((link,i) => (
            <Row className="coin-link" key={i}>
              <Title level={5} className="link-name">{link.type}</Title>
              <a href={link.url} target="_blank" rel="noreferrer">{link.name}</a>
            </Row>
          ))}
        </Col>
      </Col>
    </Col>
  );
};

export default CryptoDetails;