import React, { useEffect } from 'react';
import millify from 'millify';
import { Collapse, Row, Col, Typography, Avatar } from 'antd';
import HTMLReactParser from 'html-react-parser';

import useContextApi from '../hooks/useContextApi';
import { useState } from 'react';


const { Text } = Typography;
const { Panel } = Collapse;

const Exchanges = () => {
  const {data,error}=useContextApi();
  
  const [globaldata,cgobaldata]=useState(null);
  const [exchangesList,cexchangesList]=useState(null);
  const [errorx,cerror]=useState(null);
  useEffect(()=>{
    const options = {
      method: 'GET',
      headers: {
        'X-RapidAPI-Key': '69bb7f54damsh01d5d840abba1e9p15f017jsneb351df1d229',
        'X-RapidAPI-Host': 'coinranking1.p.rapidapi.com'
      }
    };
    
    fetch('https://coinranking1.p.rapidapi.com/coin/Qwsogvtv82FCd/exchanges?referenceCurrencyUuid=yhjMzLPhuIDl&limit=50&offset=0&orderBy=24hVolume&orderDirection=desc', options)
      .then(response => response.json())
      .then(response => {cexchangesList(response.data.exchanges)})
      .catch(err => cerror(err));
  },[]);
 

  useEffect(()=>{
    cgobaldata(data);
  },[data])

if(!exchangesList || !globaldata){
  return <div className="lds-ripple"><div></div><div></div></div>
}

if(errorx){
  return <div>{errorx}</div>
}



  return (
    <>
      <Row>
        <Col span={6}>Exchanges</Col>
        <Col span={6}>24h Trade Volume</Col>
        <Col span={6}>Markets</Col>
        <Col span={6}>Change</Col>
      </Row>
      <Row>
        {exchangesList.map((exchange,i) => (
          <Col span={24} key={i}>
            <Collapse>
              <Panel
                key={exchange.uuid}
                showArrow={false}
                header={(
                  <Row key={exchange.uuid}>
                    <Col span={6}>
                      <Text><strong>{exchange.rank}.</strong></Text>
                      <Avatar className="exchange-image" src={exchange.iconUrl} />
                      <Text><strong>{exchange.name}</strong></Text>
                    </Col>
                    <Col span={6}>${millify(exchange['24hVolume'])}</Col>
                    <Col span={6}>{millify(exchange.numberOfMarkets)}</Col>
                    <Col span={6}>{millify(Number(globaldata.coins[i].change))}%</Col>
                  </Row>
                  )}
              >
                {<div className='exchanges-hide'>
                <Avatar className="exchange-image" src={exchange.iconUrl} />
                <p><span>Price</span>: {millify(exchange.price)}</p>
                <p><span>Rank</span>: {exchange.rank}</p>
                 <Avatar className="exchange-image" src={exchange.iconUrl} />
                 </div>}
              </Panel>
            </Collapse>
          </Col>
        ))}
      </Row>
    </>
  );
};

export default Exchanges;