import React, { useEffect, useState } from 'react'
import useContextApi from '../hooks/useContextApi'
import {Row,Col,Card} from 'antd'
import {Link} from 'react-router-dom'
import millify from 'millify'
function Cryptocurrencies({simplified}) {
  const {data,error}=useContextApi();
  const [search,csearch]=useState('');
  const [coins,ccoins]=useState(null);
  const [filteredCoins,cfilteredCoins]=useState(null);
  const count=simplified?10:50;


  useEffect(()=>{
    if(data){
    const tmp=[];
    let i=0;
    while(i<count){
    tmp.push(data.coins[i]);
      ++i;
    }
    
    ccoins(tmp);
  }
  },[data]);

  useEffect(()=>{
   if(coins){
    const filtered=coins && coins.filter((x)=>{
      return x.name.toLowerCase().includes(search);
    })

    cfilteredCoins(filtered);
  }
  },[search,coins])



  if(!data){
    return <div className="lds-ripple"><div></div><div></div></div>
  }
  
 if(error){
  return <div>{error}</div>
 }

  return (
    <>
    {!simplified && <div className='search-crypto'>
      <input placeholder=' Cryptocurrency' onChange={(e)=>{
        csearch(e.target.value);
      }}></input>
    </div>}
    <Row gutter={[32,32]} className='crypto-card-container'>
      {
        filteredCoins && filteredCoins.map((coin)=>{
          return <Col xs={24} sm={12} lg={6} className='crypto-card' key={coin.uuid}>
            <Link to={`/crypto/${coin.uuid}`}>
              <Card hoverable title={`${coin.rank}. ${coin.name} `} extra={<img className='crypto-img' src={coin.iconUrl}></img>}>
                <p>Price: {millify(coin.price)}</p>
                <p>Market Cap: {millify(coin.marketCap)}</p>
                <p>Daily Change: {millify(coin.change)}</p>
              </Card>
            </Link>
          </Col>
        })
      }
    </Row>
    </>
  )
}

export default Cryptocurrencies