import { Row, Statistic,Col, Typography } from 'antd'
import { Link } from 'react-router-dom';
import React from 'react'
import millify from 'millify';
import useContextApi from '../hooks/useContextApi'
import Cryptocurrencies from './Cryptocurrencies';
import News from './News';
const {Title}=Typography;
function Homepage() {
  const {data,pending,error}=useContextApi();

  if(error){
    <div className='error'><p>{error}</p></div>
  }

  return (
   <div>
   <Title level={2} className='heading'>Global Crypto Stats</Title>
   
   <Row>
    <Col span={12}><Statistic title='Total Coins' value={data ?millify(data.stats.totalCoins):'...Loading'}></Statistic></Col>
    <Col span={12}><Statistic title='Total Exchanges' value={data ?millify(data.stats.totalExchanges):'...Loading'}></Statistic></Col>
    <Col span={12}><Statistic title='Total Markets' value={data ?millify(data.stats.totalMarkets):'...Loading'}></Statistic></Col>
    <Col span={12}><Statistic title='Total Market Cap' value={data ?millify(data.stats.totalMarketCap):'...Loading'}></Statistic></Col>
    <Col span={12}><Statistic title='Total 24h Volume' value={data ?millify(data.stats.total24hVolume):'...Loading'}></Statistic></Col>
   </Row>

   <div className='home-heading-container'>
    <Title level={2} className='home-title'>Top 10 Cryptocurrencies in the world</Title>
    <Title level={2} className='show-more'><Link to='/cryptocurrencies' >Show more</Link></Title>

   </div>
   <Cryptocurrencies simplified/>
   <div className='home-heading-container'>
        <Title level={2} className='home-title'>Latest Crypto News</Title>
          <Title level={2} className='show-more'><Link to='/news' >Show more</Link></Title>
          
    </div>
    <News simplified={true}/>
   </div>
  )
}

export default Homepage