import { Col ,Row,Typography,Card, Avatar, Select} from 'antd';
import React, { useEffect, useState } from 'react'
import moment from 'moment'
import useContextApi from '../hooks/useContextApi'
function News({simplified}) {
  const {data,pending,error}=useContextApi();
  const count=simplified?6:10;
  const {Option}=Select;
  const altimg='https://www.youtube.com/redirect?event=video_description&redir_token=QUFFLUhqbmtqMzNOM05uZkZycHRUMWJwTktuRW92MjdRd3xBQ3Jtc0tuRllqSXZuN3U2MFc3S3RwUDBEQVlqek1DU1F0aDA1Nkxzd290N1dFSTFEVGljSTZ1LXpOZlJqeTNQRHZpdWgzM0tscGNIVFZFVlVOM3Jfc1dGcFFwWDhGaVpZRVRjdUdWQzh6ckZQQXMydTFLWEpySQ&q=https%3A%2F%2Fwww.bing.com%2Fth%3Fid%3DOVFT.mpzuVZnv8dwIMRfQGPbOPC%26pid%3DNews&v=9DDX3US3kss';
const {Title,Text}=Typography;
  const [newsCategory,cnewsCategory]=useState('Cryptocurrency');
  const [filteredNews,cfilteredNews]=useState([]);
  const [newsError,cnewsError]=useState(null);
  const options = {
    method: 'GET',
    headers: {
      'X-BingApis-SDK': 'true',
      'X-RapidAPI-Key': '69bb7f54damsh01d5d840abba1e9p15f017jsneb351df1d229',
      'X-RapidAPI-Host': 'bing-news-search1.p.rapidapi.com'
    }
  };

  useEffect(()=>{
    fetch(`https://bing-news-search1.p.rapidapi.com/news/search?q=${newsCategory}&safeSearch=Off&textFormat=Raw&freshness=Day`, options)
    .then(response => response.json())
    .then(response => {
      const tmp=response.value.map((x)=>{
        return x;
      })
        
        cfilteredNews(tmp);
      }
      )
    
    .catch(err => cnewsError(err));
  },[newsCategory])




  if(newsError){
    return <div>
      <h2>{newsError}</h2>
    </div>
  }

  if(!filteredNews){
return <div className="lds-ripple"><div></div><div></div></div>
  }

  if(error){
    return <div>{error}</div>
  }

  return (
    <Row gutter={[24,24]}>
      {
        !simplified && <Col span={24}>
          <Select
            showSearch
            className='select-news'
            placeholder='Select a Crypto'
            optionFilterProp='children'
            onChange={(value)=>{
              cnewsCategory(value);
            }}

            filterOption={(input,option)=>option.children.toLowerCase().indexOf(input.toLowerCase())}
            >
              <Option value='Cryptocurrency'>Cryptocurrency</Option>
              {data && data.coins.map((c,i)=>{
                return <Option value={c.name} key={i}>{c.name}</Option>
              })}
              </Select>
        </Col>
      }
      {
         filteredNews.length > 0 ?filteredNews.map((news,i)=>{
          return <Col xs={24} sm={12} lg={8} key={i}>
            <Card hoverable className='news-card'>
              <a href={news.url} target='_blank'>
                <div className='news-image-container'>
                  <Title className='news-title' level={4}>{news.name}</Title>
                </div>
                <p>
                  {news.description > 100 ? `${news.description.substring(0,10)}...`:news.description}
                </p>
                <div className='provider-container'>
                <div>
                  <Avatar src={ news.provider[0].image ?news.provider[0].image.thumbnail.contentUrl:altimg} alt=''/>
                  <Text className='provider-name'>{news.provider[0].name}</Text>
                </div>
                <Text>{moment(news.datePublished).startOf('ss').fromNow()}</Text>
                </div>
              </a>
            </Card>
          </Col>
        }) : <div className="lds-ripple"><div></div><div></div></div>
      }
    </Row>
  )
}

export default News