import React, { useEffect, useReducer } from "react";

function reducer(state, action) {
  switch (action.type) {
    case "ISPEN":
      return { ...state, pending: true };
    case "ERR":
      return { pending: false, data: null, error: action.payload };
    case "SUCCESS":
      return { pending: false, data: action.payload, error: false };
    default:
      return { ...state };
  }
}

function useGlobalStats({ url, options }) {
  const [state, dispatch] = useReducer(reducer, {
    pending: false,
    data: null,
    error: false,
  });

  useEffect(() => {
    dispatch({ type: "PEN" });
    fetch(url, options)
      .then((response) => response.json())
      .then((response) => {
        dispatch({ type: "SUCCESS", payload: response.data });
      })
      .catch((err) => dispatch({ type: "ERR", payload: err.message }));
  }, [url]);

  return { ...state };
}

export default useGlobalStats;
